@import url("https://fonts.googleapis.com/css2?family=Jura:wght@300;400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Jura:wght@300;400;500;600;700&family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");

/* Common Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style-type: none;
  outline: none;
  font-family: "Jura", sans-serif;
}

html {
  font-size: 50%;
  scroll-behavior: smooth;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Page BG */
.page-bg {
  position: fixed;
  width: 100%;
  height: 100vh;
  background: linear-gradient(rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)),
    url(images/bg.png) center no-repeat;
  background-size: cover;
}
/* End of Page BG */

/* Navigation */
.navbar{
  position:fixed;
  top: 2.5rem;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  padding-right: 3rem;
  transition: opacity 0.5s, visibility 0.5s, transform 0.5s;
  z-index: 100;
}

.navbar-link{
  font-size: 1.5rem;
  font-weight: 700;
  text-transform: uppercase;
  color: #fff;
  margin: 0 2.5rem;
  cursor: pointer;
  text-shadow: 0 0.3rem 0.5rem #000;
}

.menu-icon{
  position: fixed;
  width: 12rem;
  height: 7rem;
  top: 0;
  right: 6rem;
  flex-direction: column;
  opacity: 0;
  visibility: hidden;
  transition: opacity .2s;
  z-index: 100;
  cursor: pointer;
}

.menu-icon-line{
  width: 4rem;
  height: .1rem;
  background-color: #fff;
  margin: 0.5rem 0;
  box-shadow: 0 .3rem .5rem #000;
}

.show-menu-icon{
  opacity: 1;
  visibility: visible;
}

.hide-navbar{
  opacity: 0;
  visibility: hidden;
  transform: translateX(10rem);
}
/* End of Navigation */

/* Mouse Circle */
.mouse-circle,
.mouse-dot {
  border-radius: 50%;
  position: fixed;
  z-index: 300;
  transform: translate(-50%, -50%);
  opacity: 0;
  pointer-events: none;
}

.mouse-circle {
  width: 6rem;
  height: 6rem;
  border: 0.1rem solid #FFE162;
  animation: mouseCircleAnim 10s infinite linear;
}

@keyframes mouseCircleAnim {
  0% {
    width: 6rem;
    height: 6rem;
  }
  25% {
    width: 8rem;
    height: 8rem;
  }
  35% {
    width: 4rem;
    height: 4rem;
  }
  70% {
    width: 8rem;
    height: 8rem;
  }
  100% {
    width: 6rem;
    height: 6rem;
  }
}

.mouse-dot {
  width: 0.5rem;
  height: 0.5rem;
  background-color: #FF6464;
  animation: mouseDotAnim 10s infinite linear;
}

@keyframes mouseDotAnim {
  0% {
    width: 0.5rem;
    height: 0.5rem;
  }
  55% {
    width: 1.5rem;
    height: 1.5rem;
  }
  100% {
    width: 0.5rem;
    height: 0.5rem;
  }
}
/* End of Mouse Circle */

.section-heading{
  font-size: 2rem;
  font-weight: 300;
  color: #fff;
  text-transform: uppercase;
  position: absolute;
  top: 50%;
  left: 10rem;
  transform: translateY(-50%);
  width: 0;
  word-break: break-all;
  line-height: 4;
}

.section-1{
    position: relative;
    width: 100%;
    height: 100vh;
    margin-bottom: 10rem;
}

.section-3{
    width: 100%;
    height: 100%;
    padding: 10rem 0 10rem 0;
    position: relative;
}

.section-4{
  position: relative;
  width: 100%;
  padding: 10rem 0 10rem 0;
}


.section-5{
  position: relative;
  height: 80vh;
}

hr{
  border-style: dotted none none;
  border-color: #FF6464;
  border-width: 4px;
  position: relative;
  width: 3%;
  margin: auto;
}


@media (max-width: 1500px) {
  .circle {
    width: 15rem;
    height: 15rem;
  }

  .main-circle {
    width: 45rem;
    height: 45rem;
  }

  .main-circle:hover {
    width: 55rem;
    height: 55rem;
  }

  .main-circle img {
    width: 160%;
    height: 160%;
  }

  .about-me-text span {
    font-size: 11rem;
  }

  .section-4 {
    margin-bottom: 10rem;
  }

  .service-btn span {
    font-size: 9rem;
  }

  .service-text {
    font-size: 4rem;
  }
}

@media (max-width: 1350px) {
  .main-btn {
    width: 12.5rem;
    height: 12.5rem;
  }

  .circle {
    display: none;
  }

  .progress-bar {
    right: 1rem;
    bottom: 0;
  }

  .about-me-text span {
    font-size: 10rem;
  }
}

@media (max-width: 1150px) {
  html {
    font-size: 55%;
  }

  .mouse-circle,
  .mouse-dot {
    display: none;
  }

  .navbar-link {
    margin: 0 2rem;
  }

  .main-circle {
    width: 40rem;
    height: 40rem;
  }


  .project-img {
    padding: 7rem 11rem;
  }

  .project-hide-btn {
    right: 2rem;
  }

  .service-btn {
    height: 12rem;
  }

  .service-btn span {
    font-size: 8rem;
  }

  .service-text {
    font-size: 3.5rem;
  }

}

@media (max-width: 900px) {
  .menu-icon {
    right: -4rem;
  }

  .navbar-link {
    font-size: 1.5rem;
    margin: 0 1rem;
  }

  .featured-text-1 {
    margin: 0 auto 0 5rem;
  }

  .featured-text-2 {
    margin: 0 5rem 0 auto;
  }

  .section-heading {
    left: 5rem;
  }

  .about-me-text span {
    font-size: 5.5rem;
    color: #ccc;
  }

  .service-btn {
    height: 9rem;
  }

  .service-btn span {
    font-size: 6rem;
  }

  .service-text {
    font-size: 3.2rem;
  }
}

@media (max-width: 700px) {
  html {
    font-size: 50%;
  }

  .contact-form-input {
    width: 50rem;
  }
}

@media (max-width: 550px) {
  html {
    font-size: 40%;
  }

  .main-circle img {
    width: 180%;
    height: 180%;
  }

  .service-btn span {
    font-size: 4.5rem;
  }

  .service-text {
    font-size: 3rem;
    color: #bbb;
  }

  .contact-form-input {
    width: 40rem;
  }

}

@media (max-width: 400px) {
  html {
    font-size: 35%;
  }

  .logo {
    font-size: 1.7rem;
    top: 13rem;
    left: 50%;
    transform: translateX(-50%);
  }

  .navbar {
    justify-content: center;
    padding: 0;
  }

  .main-circle {
    width: 38rem;
  }

  .main-circle:hover {
    width: 42rem;
  }

  .main-circle img {
    width: 200%;
    height: 200%;
  }

  .form-heading {
    font-size: 5rem;
  }
}
/* End of Responsive */